.project__swiper {
  position: relative;
  width: 100%;
}
.project__swiper-slide {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  padding: 0 10px;
  opacity: 0.5;
  filter: grayscale(100%);
  cursor: pointer;
  transition: all 0.2s ease;
}
.project__swiper-slide::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  border: 1px solid #CACACA;
}
.project__swiper-slide:hover {
  opacity: 1;
  filter: grayscale(0%);
}
.project__swiper-slide img {
  width: auto;
  height: auto;
}
.project__container-swiper {
  position: relative;
  width: 100%;
  padding: 0 75px;
}
.project__swiper-button-prev,
.project__swiper-button-next {
  position: absolute;
  top: 50%;
  left: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #ECECEC;
  cursor: pointer;
  transform: translateY(-50%);
}
.project__swiper-button-prev::after,
.project__swiper-button-next::after {
  content: '';
  position: absolute;
  top: 12px;
  left: 13px;
  width: 6px;
  height: 6px;
  border-top: 1px solid #000;
  border-right: 1px solid #000;
  transform: rotate(-135deg);
}
.project__swiper-button-next {
  left: 100%;
  transform: translate(-100%, -50%);
}
.project__swiper-button-next::after {
  top: 11px;
  left: 10px;
  transform: rotate(45deg);
}
